import { Divider } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import { Link, BrowserRouter as Router } from "react-router-dom";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import AddCircleOutlineIcon from "@material-ui/icons/AddCircleOutline";
import BookmarksIcon from "@material-ui/icons/Bookmarks";
import SearchIcon from "@material-ui/icons/Search";
import HomeWorkIcon from "@material-ui/icons/HomeWork";
import ReportIcon from "@material-ui/icons/Report";
import ShoppingBasketIcon from "@material-ui/icons/ShoppingBasket";

function MainListItems() {
  const [roleId, setRoleId] = useState("");

  useEffect(() => {
    const userval = JSON.parse(localStorage.getItem("userVal"));
    // const fullName=userVal.firstName+" "+userVal.lastName;
    // console.log("this is full Name" + userval.roleId);
    setRoleId(userval.roleId);
  }, [roleId]);

  return (
    <div>
      {roleId === 1 ? (
        <div>
          <div>
            <ListItem component={Link} to={"/Products"}>
              {["Products"].map((text, index) => (
                <ListItem button key={text}>
                  <ListItemIcon>
                    <ListItemText>
                      <ListItem component={Link} to={"/products"}>
                        <ListItemText>
                          <BookmarksIcon color="primary" />
                        </ListItemText>
                      </ListItem>
                    </ListItemText>
                  </ListItemIcon>
                  <ListItemText primary={text} />
                </ListItem>
              ))}
            </ListItem>

            <ListItem component={Link} to={"/userOrders"}>
              {["Orders"].map((text, index) => (
                <ListItem button key={text}>
                  <ListItemIcon>
                    <ListItemText>
                      <ShoppingBasketIcon color="primary" />
                    </ListItemText>
                  </ListItemIcon>
                  <ListItemText primary={text} />
                </ListItem>
              ))}
            </ListItem>
          </div>
        </div>
      ) : (
        <div />
      )}

      {roleId === 3 ? (
        <div>
          <div>
            <Divider />
            <ListItem component={Link} to={"/Orders"}>
              {["Manage Products"].map((text, index) => (
                <ListItem component={Link} to={"/AddProducts"}>
                  <ListItemIcon>
                    <ListItemText>
                      {" "}
                      <AddCircleOutlineIcon color="primary" />{" "}
                    </ListItemText>
                  </ListItemIcon>
                  <ListItemText primary={text} />
                </ListItem>
              ))}
            </ListItem>

            <ListItem button component={Link} to={"/Orders"}>
              {["See Orders"].map((text, index) => (
                <ListItem component={Link} to={"/SeeOrders"}>
                  <ListItemIcon>
                    <ListItemText>
                      <SearchIcon color="primary" />
                    </ListItemText>
                  </ListItemIcon>
                  <ListItemText primary={text} />
                </ListItem>
              ))}
            </ListItem>

            <ListItem component={Link} to={"/Management"}>
              {["User Management"].map((text, index) => (
                <ListItem button key={text}>
                  <ListItemIcon>
                    <ListItemText>
                      {" "}
                      <HomeWorkIcon color="primary" />
                    </ListItemText>
                  </ListItemIcon>
                  <ListItemText primary={text} />
                </ListItem>
              ))}
            </ListItem>
          </div>
        </div>
      ) : (
        <div />
      )}

      {roleId === 5 ? (
        <div>
          <div>
            <Divider />
            <ListItem component={Link} to={"/Usermanagement"}>
              {["User Management"].map((text, index) => (
                <ListItem button key={text}>
                  <ListItemIcon>
                    <ListItemText>
                      <HomeWorkIcon color="primary" />{" "}
                    </ListItemText>
                  </ListItemIcon>
                  <ListItemText primary={text} />
                </ListItem>
              ))}
            </ListItem>

            <ListItem component={Link} to={"/Reports"}>
              {["Reports"].map((text, index) => (
                <ListItem button key={text}>
                  <ListItemIcon>
                    <ListItemText>
                      {" "}
                      <ReportIcon color="secondary" />
                    </ListItemText>
                  </ListItemIcon>
                  <ListItemText primary={text} />
                </ListItem>
              ))}
            </ListItem>
          </div>
        </div>
      ) : (
        <div />
      )}
    </div>
  );
}

export default MainListItems;
