import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        paddingTop : '100px',
    },
}));

function SeeOrders() {
    const classes = useStyles();
   return (
      <div className={classes.root}>
         <h1>See Orders</h1>
      </div>
   );
}
export default SeeOrders;