import React, { useState, useEffect } from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import Button from "@material-ui/core/Button";
import MainListItems from "../ListItems/Listitems";
import Reports from '../SuperUser/Reports';
import UserManagement from '../SuperUser/UserManagement';
import { Switch, Route, Link, BrowserRouter as Router } from "react-router-dom";
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
const drawerWidth = 200;

const useStyles = makeStyles((theme) => ({
root: {
display: 'flex',
},
appBar: {
transition: theme.transitions.create(['margin', 'width'], {
easing: theme.transitions.easing.sharp,
duration: theme.transitions.duration.leavingScreen,
}),
},
appBarShift: {
width: `calc(100% - ${drawerWidth}px)`,
marginLeft: drawerWidth,
transition: theme.transitions.create(['margin', 'width'], {
easing: theme.transitions.easing.easeOut,
duration: theme.transitions.duration.enteringScreen,
}),
},
menuButton: {
marginRight: theme.spacing(2),
},
hide: {
display: 'none',
},
drawer: {
width: drawerWidth,
flexShrink: 0,
},
drawerPaper: {
width: drawerWidth,
},
drawerHeader: {
display: 'flex',
alignItems: 'center',
padding: theme.spacing(0, 1),
// necessary for content to be below app bar
...theme.mixins.toolbar,
justifyContent: 'flex-end',
},
user: {
display: "flex",
flex: "1",
justifyContent: "flex-end"
},
fullNameSpan: {
marginRight: '10px'
}
}));

export default function Superuser() {
const classes = useStyles();
const theme = useTheme();
const [user, setUser] = useState({});
const [open, setOpen] = useState(false);

const [userFullName, setFullNameUser] = useState("");

useEffect(() => {
const userVal = JSON.parse(localStorage.getItem("userVal"));
const fullName = userVal.firstName + " " + userVal.lastName;
console.log("this is full Name" + fullName)
setFullNameUser(fullName);
}, [userFullName]);

const handleDrawerOpen = () => {
setOpen(true);
};

const handleDrawerClose = () => {
setOpen(false);
};
const onclickHandler = () => {
localStorage.removeItem("token")
if (!user.token)
window.location = "/"
};
return (
<>
<div className={classes.root}>
<CssBaseline />
<AppBar
position="fixed"
className={clsx(classes.appBar, {
[classes.appBarShift]: open,
})}
>
<Toolbar>
<IconButton
color="inherit"
aria-label="open drawer"
onClick={handleDrawerOpen}
edge="start"
className={clsx(classes.menuButton, open && classes.hide)}
>
<MenuIcon />
</IconButton>

<Typography variant="h6" noWrap>
MYCART
</Typography>

<div className={classes.user}>
<Typography variant="h6" noWrap>
<span className={classes.fullNameSpan}>{userFullName}</span>
{/* <span className={classes.fullNameSpan}>Super User</span> */}
</Typography>
<Button
size="small"
onClick={onclickHandler}
variant="contained" color="secondary">
<ExitToAppIcon/>Logout
</Button>
</div>
</Toolbar>
</AppBar>
<Router>
<Drawer
className={classes.drawer}
variant="persistent"
anchor="left"
open={open}
classes={{
paper: classes.drawerPaper,
}}
>
<div className={classes.drawerHeader}>
<IconButton onClick={handleDrawerClose}>
{theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
</IconButton>
</div>

<List>
<MainListItems />
</List>


</Drawer>
<Switch>
<div className="App">
<Route path="/UserManagement" exact component={UserManagement} />
<Route path="/Reports" exact component={Reports} />

</div>
</Switch>
</Router>

</div>


</>
);
}