import React, { useEffect, useState } from "react";
import clsx from "clsx";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import { withStyles } from "@material-ui/core/styles";

import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import Drawer from "@material-ui/core/Drawer";
import CssBaseline from "@material-ui/core/CssBaseline";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import ListItemText from "@material-ui/core/ListItemText";
import List from "@material-ui/core/List";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import MainListItems from "../ListItems/Listitems";
import { Link, Switch, Route, BrowserRouter as Router } from "react-router-dom";
import Button from "@material-ui/core/Button";
import Orders from "../order/order";
import Products from "../../content/Products";
import SeeOrders from "../../content/SeeOrders";
import Table from "../StoreOwner/Parent";
import UserManagement from "../SuperUser/UserManagement";
import Reports from "../../content/Reports";
import Cart from "../Cart/cart";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import AccountCircle from "@material-ui/icons/AccountCircle";
import HomeIcon from "@material-ui/icons/Home";
import Deliver from "../order/deliver";
// import Carousel from '../Carousel/Carousel';
// import Carousel1 from '../Carousel/Carousel1';
// import Banners from '../Carousel/Banners';
// import Footer from '../Footer/Footer';

import UserOrders from "../../content/Orders";
import StoreOwner from "../StoreOwner/Parent";
import SuperUser from "../SuperUser/SuperUser";
import { useHistory } from "react-router-dom";
import Management from "../../content/UserManagement";

const drawerWidth = 200;

const StyledMenu = withStyles({
  paper: {
    border: "1px solid #d3d4d5",
  },
})((props) => (
  <Menu
    elevation={1}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: "bottom",
      horizontal: "center",
    }}
    transformOrigin={{
      vertical: "top",
      horizontal: "center",
    }}
    {...props}
  />
));

const StyledMenuItem = withStyles((theme) => ({}))(MenuItem);

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  appBar: {
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  hide: {
    display: "none",
  },
  gridList: {
    width: 1500,
    height: 650,
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: "flex",
    alignItems: "center",
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: "flex-end",
  },
  content: {
    // flexGrow: 1,
    // padding: theme.spacing(10),
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
  user: {
    display: "flex",
    flex: "1",
    justifyContent: "flex-end",
  },

  fullNameSpan: {
    marginRight: "10px",
  },
  popover: {
    pointerEvents: "none",
  },
  paper: {
    padding: theme.spacing(1),
  },
  image: {
    width: 128,
    height: 128,
  },
  img: {
    margin: "auto",
    display: "block",
    maxWidth: "100%",
    maxHeight: "100%",
  },
}));

export default function Dashboard() {
  let history = useHistory();
  const classes = useStyles();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("xs"));
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [user, setUser] = useState({});
  const [open, setOpen] = useState(false);
  const [userFullName, setFullNameUser] = useState("");
  const [roleId, setRoleId] = useState("");

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  useEffect(() => {
    const userVal = JSON.parse(localStorage.getItem("userVal"));
    const fullName = userVal.firstName + " " + userVal.lastName;
    setRoleId(userVal.roleId);

    setFullNameUser(fullName);
  }, [userFullName]);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const onclickHandler = () => {
    localStorage.removeItem("token");
    if (!user.token) history.push("/");
  };

  const onclickCart = () => {
    localStorage.setItem("token", user.token);
    // window.location = "/cart";
  };

  return (
    <div className={classes.root}>
      <Router>
        <CssBaseline />
        <AppBar
          position="fixed"
          className={clsx(classes.appBar, {
            [classes.appBarShift]: open,
          })}
        >
          <Toolbar>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawerOpen}
              edge="start"
              className={clsx(classes.menuButton, open && classes.hide)}
            >
              <MenuIcon />
            </IconButton>

            {/* <Link to={"/Customer"}> <span class="material-icons"><HomeIcon fontSize="large" color="secondary"/></span> </Link> */}
            <Typography variant="h6" noWrap>
              MYCART
            </Typography>
            {isMobile ? (
              <>
                <IconButton
                  className={classes.user}
                  aria-label="account of current user"
                  aria-controls="menu-appbar"
                  aria-haspopup="true"
                  onClick={handleClick}
                  color="inherit"
                >
                  <AccountCircle />
                </IconButton>
              </>
            ) : (
              <div className={classes.user}>
                <Typography variant="h6" noWrap>
                  <span className={classes.fullNameSpan}>{userFullName}</span>
                  {/* <span className={classes.fullNameSpan}>Customer</span> */}
                </Typography>

                {/* <Button
                  size="small"
                  variant="contained"
                  style={{ marginRight: "8px" }}
                  onClick={onclickCart}
                  component={Link}
                  to="/cart"
                >
                  <ShoppingCartIcon color="secondary" /> CART
                  <span class="material-icons"></span>
                </Button> */}
                {roleId == 1 ? (
                  <div>
                    {" "}
                    <Link to={"/Cart"}>
                      <Button
                        size="small"
                        variant="contained"
                        style={{ marginRight: "8px" }}
                        // onClick={onclickCart}
                      >
                        <ShoppingCartIcon color="secondary" /> CART
                        <span class="material-icons"></span>
                      </Button>
                    </Link>{" "}
                  </div>
                ) : (
                  <div></div>
                )}

                <Button
                  size="small"
                  onClick={onclickHandler}
                  variant="contained"
                  color="secondary"
                  marginRight="15"
                >
                  <ExitToAppIcon />
                  Logout
                </Button>
              </div>
            )}
            <StyledMenu
              id="customized-menu"
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={handleClose}
            >
              <StyledMenuItem>
                <span className={classes.fullNameSpan}>{userFullName}</span>
              </StyledMenuItem>
              <StyledMenuItem>
                <span className={classes.fullNameSpan}>Logout</span>
              </StyledMenuItem>
            </StyledMenu>
          </Toolbar>
        </AppBar>

        <Drawer
          className={classes.drawer}
          variant="persistent"
          anchor="left"
          open={open}
          classes={{
            paper: classes.drawerPaper,
          }}
        >
          <div className={classes.drawerHeader}>
            <IconButton onClick={handleDrawerClose}>
              {theme.direction === "ltr" ? (
                <ChevronLeftIcon />
              ) : (
                <ChevronRightIcon />
              )}
            </IconButton>
          </div>

          <List>
            <MainListItems />
          </List>
        </Drawer>
        <main
          className={clsx(classes.content, {
            [classes.contentShift]: open,
          })}
        ></main>

        <Switch>
          <div className="App">
            <Route path="/order" exact component={Orders} />
            <Route path="/products" exact component={Products} />

            <Route path="/Addproducts" exact component={Table} />
            <Route path="/userOrders" exact component={UserOrders} />
            <Route path="/Management" exact component={Management} />

            <Route path="/Usermanagement" exact component={UserManagement} />
            <Route path="/Reports" exact component={Reports} />
            {/* <Route path="/seeorder" component={seeOrder} /> */}
            <Route path="/cart" exact component={Cart} />
            <Route path="/deliver" exact component={Deliver} />
            {/* <Route path="/carousel" exact component={Carousel}/> */}
            {/* <Route path="/carousel1" exact component={Carousel1}/> */}
            {/* <Route path="/banner" exact component={Banners}/> */}
            {/* <Route path="/footer" exact component={Footer}/> */}
            {/* <Route path="/Superuser" exact component={SuperUser} /> */}
          </div>
        </Switch>
      </Router>
      {/* <div  position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}>
          <div  style={{marginTop:'100px'}}>
          <Banners/>
          </div>
          <div>
            <Link to="/products"><Button   style={{float:'right'}}
            variant="contained"
            color="primary">see more</Button></Link>
          </div>
        <Carousel/>
        <div>
            <Link to="/products"><Button  style={{float:'right'}}
            variant="contained"
            color="primary">see more</Button></Link>
          </div>
        <Carousel1/>
      <Footer/>
    </div> */}
    </div>
  );
}
