import TabulatorGrid from "./TabulatorGrid";
import React, { useEffect, useState } from "react";
import "tabulator-tables/dist/css/tabulator.min.css";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import connection from "./services/UserServices";
// import productService from "./services/getdata/productService";

const useStyles = makeStyles((theme) => ({
  products: {
    width: "50px",
  },
  button: {
    margin: theme.spacing(1),
  },
  Table: {
    // width: '100px'
    // display: "flex",
    // flex: "1",
    // justifyContent: "flex-start",
    // display: 'grid',
    // gridTemplatecolumn: '100px',
    // gridTemplateRows: '50px',
    // justifyContent: "flex-start"
    paddingTop: "70px",
    paddingRight: "200px",
    width: "500px",
    height: "500px",
    float: "left",
  },

  // nav :{
  //   marginBottom : theme.spacing(500)
  // }
}));

export default function UserManagement() {
  const classes = useStyles();
  const [childTable, setChildTable] = useState(null);

  const [selectedData, setSelectedData] = useState(null);
  const [userFullName, setFullNameUser] = useState("");
  var [status, setstatus] = useState();
  const [data, setdata] = useState({});
  const [color, setcolor] = useState("green");

  function loadData() {
    connection.getdata().then((res) => {
      console.log(res.data);
      setdata(res.data);
    });
  }
  useEffect(() => {
    loadData();
    console.log("products", JSON.stringify(data));
  }, []);

  //coming data from child
  function sendTable(list) {
    let insertObjects = JSON.parse(JSON.stringify(list.INSERT));
    let modifyObjects = JSON.parse(JSON.stringify(list.MODIFY));
    let deleteObjects = JSON.parse(JSON.stringify(list.DELETE));
    setChildTable(list);
    console.log("len", insertObjects.length);
    console.log("delete", deleteObjects);
    if (modifyObjects.length > 0) {
      connection.update(modifyObjects).then((res) => {
        if (res.status == 200) {
          setstatus("updated succesfully");
        } else {
          setcolor("red");
          setstatus("failed to update");
        }
      });
    }

    if (deleteObjects.length > 0) {
      connection.delete(deleteObjects).then((res) => {
        console.log("del res", res);

        if (res == 200) {
          setstatus("deleted succesfully");
          deleteObjects.splice(0, deleteObjects.length);
          list.DELETE.splice(0, deleteObjects.length);
          console.log("delete", JSON.stringify(deleteObjects));
          loadData();
        } else {
          setcolor("red");
          setstatus("failed to delete");
          deleteObjects.splice(0, deleteObjects.length);
        }
      });
    }

    if (insertObjects.length > 0) {
      connection.save(insertObjects).then((res) => {
        if (res == 200) {
          loadData();
          setstatus("Inserted succesfully");
          console.log(" insertobjects", insertObjects);
          insertObjects.splice(0, insertObjects.length);
          console.log("new insertobjects", insertObjects);
        } else {
          setcolor("red");
          setstatus("failed to insert");
          insertObjects.splice(0, insertObjects.length);
        }
      });
    }
  }

  return (
    <div style={{ position: "relative", top: "50px" }}>
      <br />

      <div id="status" style={{ height: "20px", color: color }}>
        {status}
      </div>
      {data.length > 0 ? (
        <div style={{ position: "relative" }}>
          <TabulatorGrid sendTable={sendTable} products={data} />
        </div>
      ) : (
        <div>loading..</div>
      )}
    </div>
  );
}
