import React from "react";
import Login from "./components/Login/Login";

import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
// import Home from '../Home';

import Placeorder from "./components/order/order";
import { Redirect } from "react-router-dom";
// import Storeowner from './components/owner/StoreOwner';
// import Superuser from './components/SuperUser/SuperUser';
// import customer from './components/Customer/customer';
// import Cart from "./components/CartItem/Cart";
import Dashboard from "../src/components/Dashboard/Dashboard";

function App() {
  return (
    <Router>
      <div className="App">
        <Switch>
          <React.Fragment>
            <Route path="/" exact component={Login} />

            <Route path="/customer" exact component={Dashboard} />
            <Route path="/storeowner" exact component={Dashboard} />
            <Route path="/superuser" exact component={Dashboard} />
            {/* <Route path="/products" exact component={Products} /> */}
            {/* <Route path="/" exact component={Login}/> */}
            {/* {(localStorage.getItem("token"))?<Route path="/customer" exact component={ Dashboard} />:<Redirect to={"/"}/>}
      {(localStorage.getItem("token"))?<Route path="/storeowner" exact component={Dashboard}/>:<Redirect to={"/"}/>}
      {(localStorage.getItem("token"))?<Route path="/superuser" exact component={Dashboard}/>:<Redirect to={"/"}/>} */}
            {/* {(localStorage.getItem("token"))?<Route path="/Cart" exact component={Cart}/>:<Redirect to={"/"}/>}
      {(localStorage.getItem("token"))?<Route path="/placeorder" exact component={Placeorder}/>:<Redirect to={"/"}/>} */}
          </React.Fragment>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
