import { Grid, makeStyles, useTheme } from "@material-ui/core";
import React, { useEffect, useState } from "react";
// import service from '../Productlist/mycart'
import Paper from "@material-ui/core/Paper";
import connection from "../StoreOwner/services/getdata/productService";
import ChildCart from "./childCart";
import SaveIcon from "@material-ui/icons/Save";
import AddToPhotosIcon from "@material-ui/icons/AddToPhotos";
import { Link } from "react-router-dom";
import { Button } from "@material-ui/core";
import productService from "../StoreOwner/services/getdata/productService";
import TextField from "@material-ui/core/TextField";
import Orders from "../order/order";
import Products from "../../content/Products";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import clsx from "clsx";
import Drawer from "@material-ui/core/Drawer";
import CssBaseline from "@material-ui/core/CssBaseline";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import List from "@material-ui/core/List";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import HomeIcon from "@material-ui/icons/Home";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import MainListItems from "../ListItems/Listitems";
import { Switch, Route, BrowserRouter as Router } from "react-router-dom";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import CloseIcon from "@material-ui/icons/Close";
import ShoppingBasketIcon from "@material-ui/icons/ShoppingBasket";
import AccountCircle from "@material-ui/icons/AccountCircle";
import { useHistory } from "react-router-dom";
const drawerWidth = 200;
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },

  paper: {
    width: "100%",
    display: "flex",
    flexWrap: "wrap",
    padding: theme.spacing(2),
    // alignContent:"center",
    justifyContent: "space-around",
    color: theme.palette.text.secondary,
  },
  delivery: {
    display: "flex",
    margin: "20px",
    width: "550px",
  },

  form: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: "25ch",
    marginTop: "20px",
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
  appBar: {
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  hide: {
    display: "none",
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: "flex",
    alignItems: "center",
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: "flex-end",
  },
  user: {
    display: "flex",
    flex: "1",
    justifyContent: "flex-end",
  },
  fullNameSpan: {
    marginRight: "10px",
  },
}));
export default function Order() {
  let history = useHistory();
  const classes = useStyles();
  const [cart, setcart] = useState({});
  const theme = useTheme();
  const [userFullName, setFullNameUser] = useState("");
  const [open, setOpen] = useState(false);
  var mycart = JSON.parse(localStorage.getItem("mycart"));
  var cartdata = [];

  const [result, setresult] = useState({});

  var productTemp = [];
  const user = JSON.parse(localStorage.getItem("userVal"));
  const [formdata, updataformdata] = useState({
    Name: "",
    MobileNo: "",
    HouseNO: "",
    addressline1: "",
    addressline2: "",
    City: "",
    Pincode: "",
  });
  const [product, setproduct] = useState({});
  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };

  // setcart(data)
  const onclickProduct = () => {
    // window.location = "/Customer";
    history.push("/Customer");
  };
  const onclickHandler = () => {
    localStorage.removeItem("token");
    // if (!user.token) window.location = "/";
    if (!user.token) history.push("/");
  };
  // setcart(data)
  useEffect(() => {
    const userVal = JSON.parse(localStorage.getItem("userVal"));
    const fullName = userVal.firstName + " " + userVal.lastName;
    console.log("this is full Name" + fullName);
    setFullNameUser(fullName);
  }, [userFullName]);

  useEffect(async () => {
    // setcart(mycart.selects)
    await connection.getdata().then((res) => {
      setproduct(res.data);
      productTemp = res.data;
    });
    if (mycart) {
      cartdata = mycart.selects;

      loadorders(cartdata, productTemp);
    } else {
      return <div> no order found..</div>;
    }
  }, []);
  let res = [];
  function loadorders(data, temp) {
    data.filter((ele, i) => {
      if (ele.counter > 0) {
        temp.filter((tele, ti) => {
          if (ele.id === tele.id) {
            let dummy = {
              ...tele,
              Qty: ele.counter,
              total: tele.price * ele.counter,
            };
            res.push(dummy);
          }
        });
      }
    });
    setresult(res);
    setcart(cartdata);
  }
  function sendOrder(order) {
    console.log("orders", order);

    let modifyOrder = JSON.parse(JSON.stringify(order.MODIFY));
    let deleteOrder = JSON.parse(JSON.stringify(order.DELETE));
    reload(modifyOrder, deleteOrder);
  }

  function reload(modify, remove) {
    let temp = result;
    if (modify.length > 0) {
      let cartTemp = [];

      modify.forEach((element, i) => {
        temp[i].total = element.price * element.Qty;
      });
      setresult(temp);

      temp.forEach((ele, i) => {});
      let dummy = [];
      temp.forEach((ele, i) => {
        let tempdummy = { id: ele.id, counter: ele.Qty };
        dummy.push(tempdummy);
      });
      let selects = { selects: dummy };
      localStorage.setItem("mycart", JSON.stringify(selects));
    }
    if (remove.length > 0) {
      console.log("delete items", remove);
      let temp = result;

      let neworder = [];
      temp.filter((ele, i) => {
        remove.filter((rele, ri) => {
          if (ele.id == rele.id) {
            temp.splice(i, 1);
            neworder.push(ele);
          }
        });
      });
      console.log("after delete ", neworder);
      setresult(temp);

      console.log("result", result);
      let dummy = [];
      temp.forEach((ele, i) => {
        let tempdummy = { id: ele.id, counter: ele.Qty };
        dummy.push(tempdummy);
      });
      let selects = { selects: dummy };
      localStorage.setItem("mycart", JSON.stringify(selects));
    }
  }

  function handleChange(e) {
    e.preventDefault();
    updataformdata({
      ...formdata,
      [e.target.name]: e.target.value,
    });
  }
  function formHandler(e) {
    e.preventDefault();
    let temp = [...result];
    let productId = [];
    let QTY = [];
    temp.filter((ele) => {
      productId.push(ele.id);
      QTY.push(ele.Qty);
    });

    let orderdetails = {
      orderStatus: "denied",
      quantity: QTY,
      productId: productId,
      storeId: "2",
      userId: user.uid,
      deliverType: "door deliver",
      orderDate: "20-20-2020",
    };
    console.log("checkout", orderdetails);
    connection.saveOrder(orderdetails);
    // window.location="/Deliver"
    history.push("/Deliver");
    console.log("data", formdata);
  }
  return (
    <div>
      {/* appbar */}
      <div className={classes.root}></div>
      <br />
      <br />
      <br />
      {/* <Typography style={{fontSize:"30px"}} color="primary">My Basket</Typography>    */}
      <div
        color="inherit"
        aria-label="open drawer"
        edge="start"
        style={{ marginLeft: "200px" }}
      >
        {result.length > 0 ? (
          <div>
            <ChildCart products={result} sendOrder={sendOrder} />
          </div>
        ) : (
          <div style={{ textAlign: "center" }}>
            <h1>Please Add Products To Cart......</h1>
          </div>
        )}
      </div>
      {/* <ChildCart products={result} checkout={ checkout }sendOrder={sendOrder}/> */}
      {/* {data.length>0?(<div>
        <Grid item md={8} xs={12}>
        <Grid item xs={12} md={4}>


        </Grid>
        <Grid item xs={12} md={4}>

        </Grid>
        <Paper className={classes.paper}>xs=3</Paper>

            
        </Grid>

        {temp}
            
        {data.map((ele,index)=>(
            <div key={index}> 
            {product.length>0?(<div></div>):(
                <div></div>
            )}
            
            <ul>
                <li key={index}> id: {ele.id}  count:{ele.count}</li>
            </ul> </div>
     ))}
    </div>):(


        <div>loading....</div>
    )} */}
    </div>
  );
}
