import { Link} from 'react-router-dom'
import { Button } from "@material-ui/core";
import productService from '../StoreOwner/services/getdata/productService';
import TextField from '@material-ui/core/TextField';
import Typography from "@material-ui/core/Typography";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    marginTop:"50px"
  },
  
  paper: {
      width:"100%",
      display:"flex" ,
      flexWrap: 'wrap',
    padding: theme.spacing(2),
    // alignContent:"center", 
    justifyContent:"space-around",
    color: theme.palette.text.secondary,
  },
  delivery:{
    display:"flex",
    margin:"20px" ,
    width:"550px",
  
  },
 
  form:{
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent:"space-around",
     
    
    
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: '25ch',
    marginTop:"20px",
 
  },
}));
export default function Order(){
        const classes=useStyles()
    const [cart,setcart]=useState({})
    var mycart=JSON.parse(localStorage.getItem("mycart"))
    var cartdata=[]
    
    const [result,setresult]=useState({})
 
    var productTemp=[]
    const user=JSON.parse(localStorage.getItem("userVal"))
    const [formdata,updataformdata]=useState({
        "Name":"","MobileNo":"","HouseNO":"",
        "addressline1":"","addressline2":"","City":"","Pincode":""
    })
const [product,setproduct]=useState({})

//    setcart(data)
   
        useEffect( async()=>{
        //    setcart(mycart.selects)
           await connection.getdata().then(  res=> {  
                      setproduct(res.data);
                      productTemp= res.data;
                     
                   })
                   if(mycart){
                       
                    cartdata=mycart.selects;

                    loadorders(cartdata,productTemp)
                   }
                   else{
                       return <div> no order found..</div>
                   }
              
    },[])   
    let res= []
   function loadorders(data,temp){
    
    data.filter((ele,i)=>{
        if(ele.counter>0){
            temp.filter((tele,ti)=>{
                if(ele.id===tele.id){
                    let dummy={...tele,'Qty':ele.counter,'total':tele.price*ele.counter}
                    res.push(dummy)
                }
            })
        }
     
  })
  setresult(res)
  setcart(cartdata)
 
   }
    function sendOrder(order){
        console.log("orders",order)

        let modifyOrder=JSON.parse(JSON.stringify(order.MODIFY));
        let deleteOrder=JSON.parse(JSON.stringify(order.DELETE));
        reload(modifyOrder,deleteOrder);
    }

    function reload(modify,remove){
     
      let temp=result;
        if(modify.length>0){
            let cartTemp=[]
            
            modify.forEach((element,i) => {
                temp[i].total=element.price*element.Qty;
                         
           
                
        });
        setresult(temp);
       
        temp.forEach((ele,i)=>{

        })
        let dummy=[]
            temp.forEach((ele,i)=>{
                let tempdummy={"id":ele.id,"counter":ele.Qty}
                dummy.push(tempdummy)
            })
            let selects={"selects":dummy}
            localStorage.setItem("mycart",JSON.stringify(selects))
        }
        if(remove.length>0){
            console.log("delete items",remove)
            let temp=result;
            let newtemp=[];
           let  neworder= [];
          temp.filter   ((ele,i)=>{
              remove.filter((rele,ri)=>{

                if(ele.id===rele.id){ 
                    temp.splice(i,1)   
                    neworder.push(ele)
                    
                }else{
                 
                   
                    
                }
            })
          })
          
          console.log("after delete ",temp);
            setresult(temp);
           
            console.log("result",result)
            let dummy=[]
            result.forEach((ele,i)=>{
                let tempdummy={"id":ele.id,"counter":ele.Qty}
                dummy.push(tempdummy)
            })
            let selects={"selects":dummy}
            localStorage.setItem("mycart",JSON.stringify(selects))
        }
          

    }
 

function handleChange(e){
 e.preventDefault();  
   updataformdata({
       ...formdata,[e.target.name]:e.target.value
   })
   
}
function formHandler(e){
    e.preventDefault()
    let temp=[...result]
    let productId=[]
    let QTY=[];
   temp.filter((ele)=>{
       productId.push(ele.id)
       QTY.push(ele.Qty)
   })
    

    let orderdetails={"orderStatus":"denied",
    "quantity":QTY,
    "productId":productId,
    "storeId":"2",
    "userId":user.uid,
    "deliverType":"door deliver",
    "orderDate":"20-20-2020",}
    console.log("checkout", orderdetails)
     connection.saveOrder(orderdetails)
window.location="/Deliver"
    console.log("data",formdata);
    
}
    return(

        <div className={classes.root}>
        <Grid container spacing={6}>
    <div className={classes.paper} > 

    <Grid item xs={10} md={5}>
    <Typography variant="h6" noWrap color="secodary"> <h2> Your order</h2> </Typography>
            
            <ChildOrder products={result} sendOrder={sendOrder}/>        
             
    </Grid>
            
         <Grid item  xs={10} md={5}>   
        <div className={classes.delivery}>
        <Paper> 

        <nav style={{color:"black", margin:"10px"}} > <Typography variant="h6" noWrap color="secodary"><h3> Deliver Address</h3></Typography></nav>
        <form className={classes.form} Validate autoComplete="on" onSubmit={formHandler}>
      <TextField id="filled-basic" label="Name" variant="outlined" name="Name" required  
      onChange={handleChange} />

      <TextField id="filled-basic"
      
       label="Mobile No" variant="outlined" name="MobileNo" required onChange={handleChange}   />

      <TextField id="standard-basic" label="HouseNO"  variant="outlined" 
      style={{marginTop:"20px"}}
      
       onChange={handleChange} 
 
 
    required />
      <TextField id="filled-basic" label="addressline1"
      style={{marginTop:"20px"}}
      
       variant="outlined" name="addressline1" required onChange={handleChange}  />
      <TextField id="outlined-basic" label="addressline2" 
      style={{marginTop:"20px"}}
      
      variant="outlined" name="addressline2"required  onChange={handleChange} />
    
   
        <TextField
          id="filled-full-width"
          label="Label"
          variant="outlined"
          style={{ margin: 8 }}
          placeholder="City"
          name="City"
      style={{marginTop:"20px"}}

          onChange={handleChange} 
          margin="normal"
          InputLabelProps={{
            shrink: true,
          }}
          required 
        />
      <TextField id="outlined-basic" 
      style={{marginTop:"20px"}}
      
      label="Pincode" variant="outlined" name="Pincode" required onChange={handleChange}  /> 
       
         <Button
        type="submit"
        style={{width:"230px" , height:"50px" , marginTop:"20px"}} variant="contained" color="primary"
        >Deliver</Button>
  
    </form>
</Paper>
        </div>
</Grid>

         {/* {data.length>0?(<div>
            <Grid item md={8} xs={12}>
            <Grid item xs={12} md={4}>


            </Grid>
            <Grid item xs={12} md={4}>

            </Grid>
            <Paper className={classes.paper}>xs=3</Paper>

                
            </Grid>

            {temp}
                
            {data.map((ele,index)=>(
                <div key={index}> 
                {product.length>0?(<div></div>):(
                    <div></div>
                )}
                
                <ul>
                    <li key={index}> id: {ele.id}  count:{ele.count}</li>
                </ul> </div>
         ))}
        </div>):(


            <div>loading....</div>
        )} */}

</div>
</Grid>
        </div>
    )
}