import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";
 
import { Grid } from "@material-ui/core/";
 
import Button from "@material-ui/core/Button";
import ButtonGroup from "@material-ui/core/ButtonGroup";



const useStyles = makeStyles((theme) => ({
  root: {
 
  },
  img:{
    width:"50%",height:"100%",
    "border-radius":"5px" 
  },
  details: {
    display: "flex",
    flexDirection: "column"
  },
  content: {
     
  },
  cover: {
     
  },
  controls: {
    display: "flex",
    alignItems: "center",
    paddingLeft: theme.spacing(1),
    paddingBottom: theme.spacing(1)
  },
  playIcon: {
    height: 38,
    width: 38
  },
 paper: {
    
  },
  grid:{
    display:"flex",
    width:"250px",
    height:"120px" 
  },
  info:{
    width:"50%"
  }
}));

export default function MediaControlCard(props) {

  const classes = useStyles();
  const[itemObj , setitemObj]=useState({id:"",selectdItems:0})
  const [state, setState] = useState(true);
  const [counter, setCount] = useState(0);
  const [stock, setStock] = useState(null);
  const [id, setId] = useState(null);
  const [incbtn, setincbtn] = useState(true);
  const [addbtn, setaddbtn] = useState(false);
  
  // console.log("props",props.id)
  // console.log('cou nter',counter)
  var handleChange = (stocks, id) => {
 
    setId(id);
    setStock(stocks);
    if (stocks === 0) {
      setincbtn(false);
      setState(true);
      setStock(stock);
      setaddbtn(true);
      // console.log("out of stock");
    } else {
      // console.log("redirect");
      setincbtn(true);
      setState(false);
      setaddbtn(true);
      setCount(counter+1)
 
    }

  };
if(typeof props.sendProducts==="function"){

  let temp={
    "id":props.id,"counter":counter
  }
  props.sendProducts(temp)
}
   var decrement = () => {
    if (counter >= 1) {
      setCount(counter - 1);
    } else {
      setaddbtn(false);
      setincbtn(false);
          }
    if(counter<1){
      setaddbtn(false);
    setincbtn(false);
    }
  };

  var increment = () => {
    if (counter < stock) {
      console.log(stock);
      setCount(counter + 1);

    }
  };

  return (
 
     
      <div className={classes.root} title={props.name}>
      
              <Grid variant="h5" gutterBottom className={classes.grid}>
              
                  
                   
                <Grid item className={classes.img}>
                 
                  
              <img style={{width:"100%",height:"100%" , borderRadius:"5px"}} src={`${process.env.PUBLIC_URL}/productImage/${props.name}.jpg`} width="80%" alt="img"/>
    
               
                  
             </Grid>
                     
              <Grid className={classes.info}>
                        <Typography gutterBottom variant="subtitle1" title={props.name}>
                           {(props.name).substring(0,8)}
                        </Typography>
                        <Typography variant="body2" gutterBottom>
                          Rs {props.price} for Kg
                        </Typography>
                        <Typography variant="body2" color="textSecondary">
                          Multiple of {props.weight}
                        </Typography>
                        <Typography variant="body2" color="textSecondary">
                          {/* Stock: {props.stock} */}
                        </Typography>

                        
                          {/* ADD buttons to redirect t oout of stocks and increment stock component */}
                          {addbtn && id === props.id ? null : (
                            <Button
                              onClick={handleChange.bind(
                                this,
                                props.stock,
                                props.id
                              )}
                              style={{ width: "120px", margin: "5px" }}
                              variant="contained"
                              color="primary"
                            >
                              ADD
                            </Button>
                          )}
                          {/* out of stock function button */}
                          {state && id ===props.id && (
                            <div>
                              <Button
                                variant="contained"
                                style={{
                                  width: "141px",
                                  padding: "2px",
                                  margin: "5px"
                                }}
                              >
                                OUT OF STOCK
                              </Button>
                            </div>
                          )}
                          {/* Increment and drecrement the stock + and - button */}
                          {incbtn && id === props.id && (
                            <div>
                              <ButtonGroup
                                size="medium"
                                style={{ margin: "4px" }}
                              >
                                <Button
                                  onClick={decrement.bind(this)}
                                  color="primary"
                                  variant="contained"
                                >
                                  -
                                </Button>

                                <Button>{counter}</Button>
                                <Button
                                  onClick={increment.bind(this)}
                                  color="primary"
                                  variant="contained"
                                >
                                  +
                                </Button>
                              </ButtonGroup>
                            </div>
                          )}
                        
                          </Grid>
              
              </Grid>
              </div>
          
        
    
  );
}