import { React, useEffect, useState } from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import {
  Card,
  CardContent,
  CardActions,
  TextField,
  Button,
  ButtonGroup,
  Link,
} from "@material-ui/core";
import "./Login.css";
import axios from "axios";
import { useHistory } from "react-router-dom";

const Login = () => {
  let history = useHistory();
  const [email, setEmail] = useState("");
  const [pwd, setPwd] = useState("");
  const [user, setUser] = useState({});
  const [errorStatus, setErrorStatus] = useState(false);
  const [emailError, setEmailError] = useState("");
  const [pwdError, setPwdError] = useState("");
  const [errorValidateEmail, setErrorValidateEmail] = useState(false);
  const [errorValidatePwd, setErrorValidatePwd] = useState(false);

  useEffect(() => {
    if (user.token) {
      localStorage.setItem("token", JSON.stringify(user.token));
      localStorage.setItem("userVal", JSON.stringify(user));
      if (user.roleId === 1) history.push("/customer");
      else if (user.roleId === 3) history.push("/storeowner");
      else if (user.roleId === 5) history.push("/superuser");
    }
  }, [user]);

  async function getUsersData() {
    let data = JSON.stringify({
      userName: email,
      pwd: pwd,
    });
    try {
      const response = await axios.post(
        "http://localhost:8090/ur/authenticate",
        data,
        { headers: { "Content-Type": "application/json" } }
      );

      console.log(response.data);
      console.log(response.status);
      setUser(response.data);
      console.log(user);
      console.log("working");
    } catch (error) {
      setErrorStatus(true);
      console.log(error.message);
    }
  }

  const validate = () => {
    if (!email.includes("@") && pwd.length < 4) {
      setEmailError("Invalid username");
      setEmail("");
      setPwdError("password should be greater than 3");
      setPwd("");
    } else if (!email.includes("@")) {
      setEmailError("Invalid username");
      setEmail("");
    } else if (pwd.length < 4) {
      setPwdError("password should be greater than 3");
      setPwd("");
    } else {
      return true;
    }
  };

  const formHandler = async (event) => {
    event.preventDefault();
    console.log("Email : ", email);
    console.log("Password : ", pwd);

    if (validate()) getUsersData();
  };

  // ######## For DEVELOPMENT ########
  function superTest() {
    setEmail("shivmunda@gmail.com");
    setPwd("shiv1");
    getUsersData();
  }

  function storeOwnerTest() {
    setEmail("swagatamandal@gmail.com");
    setPwd("swagata1");
    getUsersData();
  }

  function customerTest() {
    setEmail("viswachandreddy@gmail.com");
    setPwd("viswachand1");
    getUsersData();
  }

  // ######## For DEVELOPMENT till here ########

  const inputOneEvent = (event) => {
    setEmail(event.target.value);
    setEmailError("");
  };

  const inputTwoEvent = (event) => {
    setPwd(event.target.value);
    setPwdError("");
  };

  return (
    <>
      <AppBar position="static">
        <Toolbar variant="dense">
          <Typography variant="h6" color="inherit">
            Welcome to MYCART
          </Typography>
        </Toolbar>
      </AppBar>

      {/* {/ form /} */}

      {errorStatus && (
        <div className="Message">
          <h3>Invalid Credentials</h3>
        </div>
      )}

      <div className="cardContainer">
        <div>
          <Card className="cardForm">
            <form
              className="loginForm"
              noValidate
              onSubmit={formHandler}
              autoComplete="off"
            >
              <div className="form-container">
                <CardContent>
                  <TextField
                    id="standard-password-input"
                    label="Email"
                    type="email"
                    value={email}
                    error={errorValidateEmail}
                    helperText={emailError}
                    required
                    onChange={inputOneEvent}
                  />

                  <TextField
                    id="standard-password-input"
                    required
                    label="Password"
                    type="password"
                    value={pwd}
                    error={errorValidatePwd}
                    helperText={pwdError}
                    onChange={inputTwoEvent}
                    autoComplete="current-password"
                  />
                </CardContent>
                <CardActions className="logInButton">
                  <Button
                    type="submit"
                    size="large"
                    variant="contained"
                    color="primary"
                  >
                    Log In
                  </Button>
                </CardActions>
                <Link size="small" color="secondary">
                  Forget password?
                </Link>
              </div>
            </form>
          </Card>
        </div>
      </div>

      {/* {/ this roleCardContainer is for development only /} */}
      <div className="roleCardContainer">
        <Button
          onClick={superTest}
          variant="contained"
          style={{ marginRight: "8px" }}
          color="primary"
        >
          Customer
        </Button>
        <Button
          onClick={storeOwnerTest}
          variant="contained"
          style={{ marginRight: "8px" }}
          color="primary"
        >
          Store Owner
        </Button>
        <Button onClick={customerTest} variant="contained" color="primary">
          Super user
        </Button>
      </div>
    </>
  );
};

export default Login;
