import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        paddingTop : '100px',
    },
}));


export default function Reports() {

    const classes = useStyles();
    return (
       <div className={classes.root}>
         <h1>Reports</h1> 
       </div>
    );
    
}